import React from "react";
import "./Login.css";
import "../App.css";

const Login = (props) => {
  const loginFormSubmit = (e) => {
    e.preventDefault();
    var data = { username: e.target.username.value, password: e.target.password.value };
    if (data.username === "test-admin" && data.password === "test-admin") {
      window.alert("Login Successful !!!");
      window.localStorage.setItem("userType", "storemanager");
      props.history.push("/products");
    } else if (data.username === "test-sales" && data.password === "test-sales") {
      window.alert("Login Successful !!!");
      window.localStorage.setItem("userType", "salesexecutive");
      props.history.push("/orders");
    } else {
      window.alert("Please Enter Valid Credential !!!");
    }
  };
  return (
    <>
      <div className="form-wrapper">
        <form onSubmit={loginFormSubmit}>
          <div className="inner-form-wrapper">
            <img
              alt="login-icon"
              src="https://www.seekpng.com/png/full/428-4287240_no-avatar-user-circle-icon-png.png"
              width="70px"
            ></img>

            <h2>Login Credentials</h2>
            <input type="text" name="username" placeholder="Username"></input>
            <input type="password" name="password" placeholder="Password"></input>
            <input id="submit-btn" type="submit" value="Log In"></input>
          </div>
        </form>
      </div>
    </>
  );
};

export default Login;
