import { useEffect, useState } from "react";
import React from "react";
import Table from "./Table";
import Topbar from "./Topbar";

const cols = [
  { field: "orderid", headerName: "Order ID", width: 200 },
  { field: "customerName", headerName: "Customer Name", width: 200, editable: true },
  {
    field: "productName",
    headerName: "Product name",
    width: 200,
    editable: true,
  },
  {
    field: "contact",
    headerName: "Customer Contact",
    width: 200,
    editable: true,
  },

  {
    field: "quantity",
    headerName: "Quantity",
    width: 150,
  },
  {
    field: "price",
    headerName: "Price",
    width: 150,
  },
  {
    field: "totalAmount",
    headerName: "Total Amount",
    width: 100,
  },
];
const Orders = () => {
  const [medForVis, setMedForVis] = useState("none");
  let ordersData = JSON.parse(window.localStorage.getItem("ordersData"));
  if (ordersData == null) {
    ordersData = [
      {
        id: 0,
        orderid: "null",
        customerName: "null",
        contact: "null",
        productName: "null",
        quantity: "null",
        price: "null",
        totalAmount: "null",
      },
    ];
  }
  let date = new Date().getTime();
  let orderId = "ID" + date;
  const [dataForDisplay, setDataForDisplay] = useState(ordersData);
  return (
    <>
      <Topbar />
      <div className="outer-wrapper">
        <div className="product-header">
          <h1>All Orders</h1>
          <button
            onClick={() => {
              setMedForVis("block");
            }}
          >
            Create Order
          </button>
        </div>
        <div className="inner-wrapper">
          <Table rows={dataForDisplay} cols={cols}></Table>
        </div>
      </div>

      <div className="medicineForm" style={{ display: medForVis }}>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            var totalPrice = Number(e.target.quantity.value) * Number(e.target.price.value);
            var data = {
              id: new Date().getTime(),
              orderid: orderId,
              customerName: e.target.customerName.value,
              contact: e.target.contact.value,
              productName: e.target.product.value,
              quantity: "x" + e.target.quantity.value,
              price: "Rs. " + e.target.price.value,
              totalAmount: "Rs. " + totalPrice,
            };
            let ordersData = window.localStorage.getItem("ordersData");
            if (ordersData === null) {
              ordersData = [];
              ordersData.push(data);
              window.localStorage.setItem("ordersData", JSON.stringify(ordersData));
            } else {
              ordersData = JSON.parse(window.localStorage.getItem("ordersData"));
              ordersData.push(data);
              window.localStorage.setItem("ordersData", JSON.stringify(ordersData));
            }
            setMedForVis("none");
            setDataForDisplay(ordersData);
          }}
        >
          <input type="text" name="customerName" required placeholder="Customer Name"></input>
          <input type="text" name="contact" required placeholder="Contact Number"></input>
          <input type="text" name="product" required placeholder="Product Name"></input>
          <input type="text" name="quantity" required placeholder="Quantity"></input>
          <input type="text" name="price" required placeholder="Price"></input>
          <input id="sub" type="submit" value="Save"></input>
        </form>
      </div>
    </>
  );
};

export default Orders;
