import { useEffect, useState } from "react";
import React from "react";
import Table from "./Table";
import Topbar from "./Topbar";

const cols = [
  {
    field: "firstName",
    headerName: "First Name",
    width: 250,
    editable: true,
    headerAlign: "left",
  },
  {
    field: "lastName",
    headerName: "Last Name",
    width: 250,
    editable: true,
    headerAlign: "left",
  },
  {
    field: "dob",
    headerName: "Date of Birth",
    type: "date",
    width: 250,
    editable: true,
    headerAlign: "left",
  },
  {
    field: "gender",
    headerName: "Gender",
    width: 250,
    editable: true,
    headerAlign: "left",
  },
  {
    field: "experience",
    headerName: "Experience",
    width: 200,
    editable: true,
    headerAlign: "left",
  },
];

const Users = () => {
  const [medForVis, setMedForVis] = useState("none");
  let userData = JSON.parse(window.localStorage.getItem("userData"));
  if (userData == null) {
    userData = [
      {
        id: 0,
        firstName: "null",
        lastName: "null",
        dob: "null",
        gender: "null",
        experience: "null",
      },
    ];
  }
  const [dataForDisplay, setDataForDisplay] = useState(userData);
  return (
    <>
      <Topbar />
      <div className="outer-wrapper">
        <div className="product-header">
          <h1>All Medicines</h1>
          <button
            onClick={() => {
              setMedForVis("block");
            }}
          >
            Add Executives
          </button>
        </div>
        <div className="inner-wrapper">
          <Table rows={dataForDisplay} cols={cols}></Table>
        </div>
      </div>
      <div className="medicineForm" style={{ display: medForVis }}>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            var data = {
              id: new Date().getTime(),
              firstName: e.target.firstName.value,
              lastName: e.target.lastName.value,
              dob: e.target.dob.value,
              gender: e.target.gender.value,
              experience: e.target.experience.value,
            };
            let userData = window.localStorage.getItem("userData");
            if (userData === null) {
              userData = [];
              userData.push(data);
              window.localStorage.setItem("userData", JSON.stringify(userData));
            } else {
              userData = JSON.parse(window.localStorage.getItem("userData"));
              userData.push(data);
              window.localStorage.setItem("userData", JSON.stringify(userData));
            }
            setMedForVis("none");
            setDataForDisplay(userData);
          }}
        >
          <input type="text" name="firstName" required placeholder="First Name"></input>
          <input type="text" name="lastName" required placeholder="Last Name"></input>
          <input type="date" name="dob" required placeholder="Date of Birth"></input>
          <input type="text" name="gender" required placeholder="Gender"></input>
          <input type="text" name="experience" required placeholder="Exprience"></input>
          <input id="sub" type="submit" value="Save"></input>
        </form>
      </div>
    </>
  );
};

export default Users;
