import { NavLink } from "react-router-dom";
import { useHistory } from "react-router-dom";
import React from "react";
import "./Topbar.css";

const Topbar = () => {
  const history = useHistory();
  const loginrole = window.localStorage.getItem("userType");
  return (
    <>
      <div className="topbar-wrapper">
        <div className="topbar-left">
          <h1>PHARMACY.</h1>
        </div>
        {loginrole === "storemanager" ? (
          <div className="topbar-mid">
            <NavLink activeClassName="active" className="menu" to="/products">
              Products
            </NavLink>
            <NavLink activeClassName="active" className="menu" to="/orders">
              Orders
            </NavLink>
            <NavLink activeClassName="active" className="menu" to="/users">
              Users
            </NavLink>
          </div>
        ) : (
          <div className="topbar-mid">
            <NavLink activeClassName="active" className="menu" to="/orders">
              Orders
            </NavLink>
          </div>
        )}
        <div className="topbar-right">
          <button
            onClick={() => {
              history.push("/");
            }}
          >
            Sign Out
          </button>
        </div>
      </div>
    </>
  );
};

export default Topbar;
